body {
    background-color: aqua;
}
form {
    padding: 1.0rem;
  }
  
  .form-control {
    margin-bottom: 1rem;
  }
  
  .form-control label {
    display: block;
    font-weight: bold;
    margin-bottom: 0.5rem;
  }
  
  .form-control input,
  .form-control textarea {
    display: block;
    font: inherit;
    border-radius: 4px;
    border: 2px solid #ccc;
    padding: 1.5rem;
    width: 100%;
  }
  
  .actions {
    margin-top: 1rem;
    text-align: right;
  }
  
  .btn {
    font: inherit;
    cursor: pointer;
    background-color: #77002e;
    color: white;
    padding: 1.0rem 1.0rem;
    border: 1px solid #77002e;
    border-radius: 3px;
    font-weight: bold;
    text-transform: uppercase;
  }
  
  .btn:hover,
  .btn:active {
    background-color: #a50e48;
    border-color: #a50e48;
  }